#page-logo {
  font-family: "Product Sans", monospace;
  font-size: 30px;
  margin-left: 5px;
  margin-top: -2px;
}
#page-logo-icon {
  margin-right: 1px;
}
.materialize-indigo {
  background-color: #3a53af !important;
}

.reactColor {
  color: #62dafc;
}

.checkboxes {
  text-align: center;
}

.checkboxes input {
  margin: 0px 0px 0px 0px;
}

.gradient-45deg-indigo-purple {
  background: -webkit-linear-gradient(45deg, #303f9f, #7b1fa2) !important;
  background: linear-gradient(45deg, #303f9f, #7b1fa2) !important;
}
.gradient-45deg-indigo-purple.gradient-shadow {
  box-shadow: 0 6px 20px 0 rgba(123, 31, 162, 0.5) !important;
}

.gradient-45deg-purple-deep-orange {
  background: -webkit-linear-gradient(45deg, #8e24aa, #ff6e40) !important;
  background: linear-gradient(45deg, #8e24aa, #ff6e40) !important;
}
.gradient-45deg-purple-deep-orange.gradient-shadow {
  box-shadow: 0 6px 20px 0 rgba(255, 110, 64, 0.5) !important;
}

.gradient-45deg-cyan-light-green {
  background: -webkit-linear-gradient(45deg, #0097a7, #b2ff59) !important;
  background: linear-gradient(45deg, #0097a7, #b2ff59) !important;
}
.gradient-45deg-cyan-light-green.gradient-shadow {
  box-shadow: 0 6px 20px 0 rgba(178, 255, 89, 0.5) !important;
}
.checkboxes label {
  margin: 0px 20px 0px 3px;
}

.gradientShadow {
  box-shadow: 0 10px 35px 0 rgb(168, 166, 166) !important;
}

.materialize-indigo {
  background-color: #3a53af !important;
}

#info-svg-signup {
  margin-top: 100px;
}

#info-svg-teacher {
  margin-top: 80px;
}

.contents {
  margin-top: 80px;
}

a :hover {
  border-bottom: 1px solid #1a73e9;
}

.bgc {
  background-color: #1a73e9;
}

.nameBlock {
  border: 1px solid #1a73e9;
  border-radius: 10px;
}

.fgc {
  color: #1a73e9;
}

.tabs .tab a {
  color: #478ae2;
} /*Black color to the text*/

.tabs .tab a:hover {
  background-color: #d7dbe0;
  color: #3d87e7;
} /*Text color on hover*/

.tabs .tab a.active {
  background-color: #d9dadb;
  color: #043575;
  font-weight: bold;
}

.tabs .indicator {
  background-color: #094faa;
}

.circleSilver {
  border: 1px solid #616161;
  border-radius: 50%;
  padding: 2rem;
}
.circleGolden {
  border: 1px solid #ffb300;
  border-radius: 50%;
  padding: 2rem;
}
.circlePlatinum {
  border: 1px solid #0097a7;
  border-radius: 50%;
  padding: 2rem;
}

#info-svg-signin {
  width: 80%;
}

.section {
  height: 60vh;
  box-sizing: border-box;
  padding: 30px;
  margin: 0;
}

.backgroundImage {
  background-image: url("../img/bg.jpg");
}

.textShadow {
  text-shadow: 2px 2px #1a73e9;
}

.landingPageParagraph {
  line-height: 2.5;
}

.btn {
  font-weight: 500;
  color: #ebf9ff;
  text-transform: capitalize;
}

.btn:hover {
  background-color: #1a73e9;
}

.subsBtn {
  width: 80%;
  border-radius: 15px;
}

.borderRadius {
  border-radius: 5px;
}

img {
  height: 200px;
}
.card {
  border-radius: 10px;
}

.modal {
  width: 50%;
}

.nav-fixed {
  position: fixed;
  right: 0;
  left: 0;
  z-index: 999;
}

.nav-fixed {
  top: 0;
}

#classCards {
  margin-top: 100px;
}

#reg {
  margin-top: 60px;
}

#load {
  margin-top: 64px;
}

h2 {
  font-family: "Product Sans", monospace;
}

