#dropdown1 {
  width: 220px !important;
}

@keyframes animate {
  0% {
    background-position: -500%;
  }
  100% {
    background-position: 500%;
  }
}

/*mobile*/
/*mobile*/
@media only screen and (max-width: 600px) {
  #load {
    margin-top: 55px;
  }
  #info-svg-signup {
    display: none;
  }
  #info-svg-signin {
    display: none;
  }
  #info-svg-teacher {
    display: none;
  }
}

.justifyText {
  text-align: justify;
  text-justify: inter-word;
}

/*rounded*/
.card {
  border-radius: 9px;
}

/*Profile Card*/

#profile-card .card-image {
  height: 150px;
}

#profile-card .card-profile-image {
  position: absolute;
  z-index: 1;
  top: 110px;

  width: 70px;

  cursor: pointer;
}

#profile-card .btn-move-up {
  position: relative;
  top: -40px;
  right: -18px;

  margin-right: 10px !important;
}

#profile-card .card-content .profile-card-i {
  position: relative;
  top: 6px;

  margin-right: 10px;
}

#profile-card .card-content p {
  font-size: 1rem;

  margin: 10px 0 12px;
}

#profile-card .card-reveal i.material-icons {
  position: relative;
  top: 6px;
}
